// ** React Imports
import { Fragment, useState, useEffect, memo } from 'react'
import UiLoader from '@components/ui-loader'

// ** Store & Actions
import { GetUserContests } from '../../../../../store/actions'
import { useSelector, useDispatch } from 'react-redux'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'

// ** Third Party Components
import ReactPaginate from 'react-paginate'
import { ChevronDown } from 'react-feather'
import DataTable from 'react-data-table-component'
import { Card, Input, Label, Row, Col } from 'reactstrap'
import { useParams } from 'react-router-dom'
import BreadCrumbs from '@components/breadcrumbs'
const DataTableTransaction = () => {

  // ** Store Vars

  const dispatch = useDispatch()
  const store = useSelector(state => state.users)
  const params = useParams()
  // ** States

  const [currentPage, setCurrentPage] = useState(1)
  const [rowsPerPage, setRowsPerPage] = useState(10)
  const [searchValue, setSearchValue] = useState('')
  const [filteredData, setFilteredData] = useState([])

  // ** Get data on mount
  
  useEffect(() => {    
    dispatch(GetUserContests(params.id, currentPage))
  }, [currentPage])

  // ** Function to handle filter

  const handleFilter = e => {
    const value = e.target.value
    let updatedData = []
    setSearchValue(value)
    if (value.length) {
      updatedData = store.contests.data.filter(item => {

        const startsWith =
        item.total_prize.toString().toLowerCase().startsWith(value.toLowerCase()) ||
        item.entry_fee.toString().toLowerCase().startsWith(value.toLowerCase()) ||
        item.contest_name.toLowerCase().startsWith(value.toLowerCase()) ||
        item.contest_type.toLowerCase().startsWith(value.toLowerCase()) ||
        item.caption.toLowerCase().startsWith(value.toLowerCase()) 
      
      const includes =
        item.total_prize.toString().toLowerCase().includes(value.toLowerCase()) ||
        item.entry_fee.toString().toLowerCase().includes(value.toLowerCase()) ||
        item.contest_name.toLowerCase().includes(value.toLowerCase()) ||
        item.contest_type.toLowerCase().includes(value.toLowerCase()) ||
        item.caption.toLowerCase().includes(value.toLowerCase()) 

        if (startsWith) {
          return startsWith
        } else if (!startsWith && includes) {
          return includes
        } else return null
      })
      setFilteredData(updatedData)
    }
  }

  // ** Function to handle Pagination and get data

  const handlePagination = page => {
    setCurrentPage(page.selected + 1)
  }

  // ** Function to handle per page

  const handlePerPage = e => {
    setRowsPerPage(parseInt(e.target.value))
  }

  // ** Custom Pagination
  const CustomPagination = () => {

    const count = store.contests.last_page
    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        breakLabel='...'
        pageCount={count}
        marginPagesDisplayed={2}
        pageRangeDisplayed={2}
        activeClassName='active'
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={page => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        breakClassName='page-item'
        breakLinkClassName='page-link'
        containerClassName={
          'pagination react-paginate separated-pagination pagination-sm justify-content-end pr-1 mt-1'
        }
      />
    )
  }
console.log(store.contests.data)
  // ** Table data to render
  const dataToRender = () => {
    if (store.contests.data?.length > 0) {
      return searchValue.length ? filteredData : store.contests.data.slice(0, rowsPerPage)
    } else {
      return []
    } 
  }

  if (store.loadAllUsers) {
    return (
      <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
        <UiLoader
          blocking
        />
      </div>
    )
  }

  const columns = [
    {
      name: 'Sno',
      minWidth: '10px',
      selector: 's_no',
      sortable: true,
      cell: (row, pos) => `#${store.contests.from + pos}`
    }, 
    {
      name: 'Contest Name',
      minWidth: '100px',
      selector: 'contest_name',
      sortable: true,
      cell: (row) => row?.contest_name 
    },
    {
      name: 'Type',
      minWidth: '100px',
      selector: 'type',
      sortable: true,
      cell: (row) => row?.contest_type
    },
    {
      name: 'Caption',
      minWidth: '100px',
      selector: 'caption',
      sortable: true,
      cell: (row) => row?.caption
    },
    {
      name: 'Entry Fee',
      minWidth: '100px',
      selector: 'entry_fee',
      sortable: true,
      cell: (row) => row?.entry_fee
    },
    {
      name: 'Total Prize',
      minWidth: '100px',
      selector: 'total_prize',
      sortable: true,
      cell: (row) => row?.total_prize
    }
  ]

  return (
    <Fragment>
      <BreadCrumbs breadCrumbTitle='Manage Users' breadCrumbParent='users'  breadCrumbActive='contests' />
      <Card>
        <Row className='mx-0 mt-1 mb-50'>
          <Col sm='6'>
            <div className='d-flex align-items-center'>
              <Label for='sort-select'>show</Label>
              <Input
                className='dataTable-select'
                type='select'
                id='sort-select'
                value={rowsPerPage}
                onChange={e => handlePerPage(e)}
              >
                <option value={10}>10</option>
                <option value={50}>50</option>
                <option value={100}>100</option>
              </Input>
              <Label for='sort-select'>entries</Label>
            </div>
          </Col>
          <Col className='d-flex align-items-center justify-content-sm-end mt-sm-0 mt-1' sm='6'>
            <Label className='mr-1' for='search-input'>
              Search
            </Label>
            <Input
              className='dataTable-filter'
              type='text'
              bsSize='sm'
              id='search-input'
              value={searchValue}
              onChange={handleFilter}
            />
          </Col>
        </Row>
        <DataTable
          noHeader
          pagination
          paginationServer
          className='react-dataTable'
          columns={columns}
          sortIcon={<ChevronDown size={10} />}
          paginationComponent={CustomPagination}
          data={dataToRender()}
        />
      </Card>
    </Fragment>
  )
}

export default memo(DataTableTransaction)
